$app-picker: #{$prefix}-app-picker;
$wrapper: #{$app-picker}__wrapper;
$grid: #{$app-picker}__grid;
$row: #{$app-picker}__row;
$chip: #{$app-picker}__chip;
$chip-label: #{$app-picker}__chip-label;
$title: #{$app-picker}__section-title;
$separator: #{$app-picker}__separator;
$arrow: #{$app-picker}__arrow;


.#{$wrapper} {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-bottom: 4px;
  margin: 0 auto;
  &:focus {
    outline: none;
  }
}

.#{$arrow} {
  position: relative;
  top: -21px;
  margin: 0;
  fill: #FFF;
}

.#{$grid} {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0 8px;
}

.#{$row} { 
  display: flex; 
  width: 100%;
}

.#{$title} {
  color: color('neutral', 500);
  font-size: size(m);
  font-weight: bold;
  margin: 12px 0 4px;
}

.#{$chip} {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 2%;
  margin: 4px;
  padding: 6px;
  height: 68px;
  width: 68px;
  color: color('neutral', 500);
  cursor: pointer;
  &:focus,
  &:hover {
    @include dsFocus();
    color: color('neutral', 700);
    background-color: color('neutral', 000);
  }
  &:hover .app-picker__icon {
    fill: color('brand-primary', 800) ;
  }
  &:active {
    @include dsActive();
    box-shadow: none;
    background-color: #ECF8FF;
  }
  &--disabled {
    color: color('neutral', 500);
    &:focus,
    &:active,
    &:hover {
      background-color: #FCFCFC;
      color: color('neutral', 500);
      border: 1px solid color('neutral', 100);
      outline: none;
      outline-offset: none;
      box-shadow: none;
    }
    &:hover .app-picker__icon {
      fill: color('neutral', 500) ;
    }
  }
  &--disabled .app-picker__icon {
    fill: color('neutral', 500);
  }
}

.app-picker__icon {
  fill: color('brand-primary', 600)
}

.#{$chip-label} {
  font-size: 12px;
  margin: 5px;
}

.#{$separator} {
  border-top: 1px solid color('neutral', 100);
  border-bottom: none;
  width: 99%;
  margin: 0;
}