
$menu: #{$combo}__menu;
$combo-trigger: #{$combo}-trigger;

.#{$menu} {
    margin: 0;
    top: 100%;
    background: color(neutral, 0);
    @include border-radius($radius);
    border: solid 1px color(neutral, 400);
    @include box-shadow(depth(300));
    margin-bottom: 8px;
    margin-top: space(xxs) * 0.5;
    position: absolute;
    width: 100%;
    box-sizing: border-box;
  }

.#{$combo-trigger} {
    position: relative;
  }

.em-ds-layout__header {
  z-index: 1;
}
