$primary-text-color: color(neutral, 900);

:root {
  font-family: $theme-font-family;
  font-size: $theme-base-font-size;
  color: $primary-text-color;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

h1 {
  @include h1-text-style;
}

h2 {
  @include h2-text-style;
}

h3 {
  @include h3-text-style;
}

h4 {
  @include h4-text-style;
}

p {
  @include body-text-style;
}

a {
  @include link-text-style;
}

.#{$prefix}-h1-text-style {
  @include h1-text-style;
}

.#{$prefix}-h2-text-style {
  @include h2-text-style;
}

.#{$prefix}-h3-text-style {
  @include h3-text-style;
}

.#{$prefix}-h4-text-style {
  @include h4-text-style;
}

.#{$prefix}-section-header-text-style {
  @include section-header-text-style;
}

.#{$prefix}-body-text-style {
  @include body-text-style;
}

.#{$prefix}-body-small-text-style {
  @include body-small-text-style;
}

.#{$prefix}-link-text-style {
  @include link-text-style;
}
