$ph2-menu: #{$ph2}-menu;
$btn-el-secondary: #{$prefix}-button--secondary;

.#{$ph2-menu} {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: space(xxs);
  .#{$btn-el-secondary} {
    &:not(:hover){
      border-color: transparent;
    }
    &:focus:after{
      top: -1px;
      left: -1px;
      width: calc(100% + 2px);
      height: calc(100% + 2px);
    }
  }

}
