$ph2-toolbar: #{$ph2}-toolbar;
$ph-toolbar: #{$prefix}-toolbar;
$ph-btn: #{$prefix}-button;
$ph-input-search: #{$prefix}-input-addon-wrapper;

.#{$ph2-toolbar} {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  .#{$ph-toolbar} {
    height: auto;
    background: none;
    & > div {
      align-items: center;
      .#{$ph-btn} {
        margin-left: space(m) * 0.5;
      }
      .#{$ph-input-search}{
        box-shadow: none;
        div {
          height: 100%;
        }
      }
    }
  }
}
