// ********************************************
// container definition
$overlay-container: #{$prefix}-overlay;

// ********************************************
// elements definition
$el-backdrop: #{$prefix}-overlay__backdrop;


@mixin position-all($position, $value) {
  position: $position;
  top: $value;
  right: $value;
  bottom: $value;
  left: $value;
}

.#{$overlay-container} {
  @include position-all(static, 0);

  &--with-scroll {
    position: fixed;
  }

  &--inline {
    display: inline;
    overflow: visible;
  }
}

.#{$el-backdrop} {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: $theme-base-backdrop-opacity;
  z-index: 2;
}


