$icon-unit: 8px !default;

$icon-data: (
  none: 0,                           
  xxs:  ($icon-unit / 2), // 4px
  xs:   ($icon-unit),     // 8px
  s:    ($icon-unit * 2), // 16px
  m:    ($icon-unit * 3), // 24px
  l:    ($icon-unit * 4), // 32px
  xl:   ($icon-unit * 6), // 48px
  xxl:  ($icon-unit * 8)  // 64px
) !default;