$container: #{$prefix}-query-builder-output;
$accordion: #{$prefix}-accordion;
$accordion-item: #{$accordion}-item;

.#{$container}{
  padding: space(xs) 0;
  // .#{$accordion}{}
  .#{$accordion-item}, 
  {
    border: none;
    &:last-child{
      border: none;
    }
    &__title{
      color: color(brand-primary, 700);
      h2{
        @include body-text-style(semibold);
        line-height: space(l);
        text-transform: uppercase;
      }
    }
    &__expand-zone-space{
      padding-top: 0;
    }
  }

}
