// ********************************************
// container definition
$bar-container: #{$prefix}-filter-bar;

// ********************************************
// element definition
$pills-el: #{$prefix}-filter-bar__pills;
$buttons-el: #{$prefix}-filter-bar__buttons;

.#{$bar-container} {
  display: flex;
  margin-bottom: space(xs);
}

.#{$pills-el} {
  display: flex;
  flex: 1;
  align-items: center;
  flex-wrap: wrap;
}

.#{$buttons-el} {
  margin-left: space(xs);
}
