// ********************************************
// container definition
$popover-container: #{$prefix}-popover;

// ********************************************
// container definition
$el-text: #{$prefix}-tooltip__text;

$radius: $theme-base-border-radius;

$max-width: space(xxl) * 3.9;

.#{$popover-container} {
  z-index: 0;
  text-align: center;
  max-width: $max-width;
  min-width: space(xl) * 1.17;
  padding: space(xs) space(s);
  background-color: color(neutral, 000);

  // text styles in container
  white-space: normal;
  word-wrap: break-word;
  display: inline-block;
  color: color(neutral, 600);

  @include border-radius($radius);
  @include box-shadow(depth(200, 'alt'));
}

.#{$popover-container}__arrow {
  z-index: 100;
  position: absolute;
  width: 18px;
  height: 18px;
  pointer-events: none;

  & .stroke {
    fill: color(neutral, 500);
    fill-opacity: .4;
  }
  & .fill {
    fill: color(neutral, 000);
  }

  // popper overrides top property, we have to define it here
  &[data-placement^="top"] {
    top: 100%;
  }
  &[data-placement^="bottom"] {
    margin-bottom: -0.5px;
  }

}
