$cat: #{$group-box-content}--category-box;
$group: #{$group-box-wrapper}--group-box;

.#{$cat} {
  .#{$group}:first-child {
    border-left: none;
  }
  .#{$group}:last-child {
    border-right: none;
  }

  .#{$group-box-wrapper} {
    .#{$group-box-content} {
      padding-left: space(s);
    }
  }
  .#{$group-box-wrapper}:first-child {
    .#{$group-box-content} {
      border-left: none;
    }
  }
  .#{$group-box-wrapper}:last-child {
    .#{$group-box-content} {
      border-right: none;
    }
  }
}

.#{$group-box-wrapper}--no-border {
  .#{$cat} {
    .#{$group-box-wrapper}:first-child {
      .#{$group-box-content} {
        border-left: $theme-base-border-style color(neutral, 200);
      }
      .#{$group-box-content}--no-border {
        border-left: none;
      }
    }

    .#{$group-box-wrapper}:last-child {
      .#{$group-box-content} {
        border-right: $theme-base-border-style color(neutral, 200);
      }
      .#{$group-box-content}--no-border {
        border-right: none;
      }
    }
  }
}



.#{$group} {
  & > .#{$cat} {
    padding-top: 0;
  }
}
