$menu-combo: #{$menu-container}--menu-combo-box;

.#{$menu-combo}{
  padding: 0;
  padding-top: space(xs);
  border: $theme-base-border-style color(neutral, 200);
  .#{$prefix}-combobox__menu{
    margin-bottom: 0;
    position: initial;
    border: none;
  }
  .#{$prefix}-combobox__control{
    margin-left: space(xs);
    margin-right: space(xs);
  }
}