$wrapper: #{$prefix}-start-date-time-picker;
$inputs: #{$prefix}-start-date-time-picker-inputs;


$label: #{$wrapper}__label;
$input-time-outline: #{$prefix}-labeled-outline;
$icon-btn:  #{$prefix}-icon-button;

.#{$wrapper}{
  padding: space(s) 0;
  margin: space(xs) 0;
  // border-top: $border-row-separator;
  // border-bottom: $border-row-separator;

  .#{$input-group}{
    flex: 1;
    & > div{
      width: 50%;

      .#{$input-time-outline}{
        margin-top: 0;
      }
    }
    .#{$icon-btn}{
      outline: none;
    }
  }
}

.#{$label}{
  @include labelStyle();
}

.#{$prefix}-datepicker-range-inputs{
  display: flex;
  .SingleDatePicker{
    flex: 1;
    .SingleDatePickerInput,
    .DateInput{
      width: 100%;
    }
  }
}

.#{$prefix}-modal.datetime-alert-modal{
  z-index: 112;
}
