$ph2-title: #{$ph2}-title;
$input: #{$prefix}-input;
$input-container: editable-title-component;

.#{$ph2-title} {
  &__editable-title {
    @include h2-text-style();
    padding-left: space(xs);
  }
}
.#{$input-container} {
  position: relative;
  left: space(xs) * (-1);
  top: 1px;
  margin-left: -1px;
  margin-right: 1px;
}
