// ********************************************
// container definition
$tree-list: #{$prefix}-tree-list;
$tree-item: #{$prefix}-tree-item;
$rz-normal: #{$prefix}-tv-row-size-normal;
$rz-compact: #{$prefix}-tv-row-size-compact;
$normal: 36px;
$compact: 28px;

$dsicon: #{$prefix}-icon;

.#{$tree-item} {  
  color: color(neutral, 800);
  display: flex;
  position: relative;
  background-color: color(neutral, 000);
  padding: 0 space(xxs);
  min-height: $normal;

  .#{$tree-item}__value {
    display: flex;
    flex: 1;
    min-width: 0;
    margin: space(xs) 0 ;
    margin-top: space(xs) * 1.5;
    .tree-item-value {
      margin-right: 2px;
    }
  }

  &__addons {
    display: flex;
    align-items: center;
    height: $normal;
    margin-right: space(xxs) * 0.5;
    .#{$dsicon} {
      @include icon-color(color(brand-primary, 800));
    }
  }

  &.selected {
    background-color: color(brand-primary, 200);
  }

  .drag-handle {
    transition: duration(fast) easing(base);
    align-items: center;
    display: flex;
    height: 100%;
    width: space(s) * 1.3;
    visibility: visible;
    opacity: 1;
    // todo: revise this on the future to get icon proper sizes
    svg {
      width: 20px;
      height: 20px;
    }

    &:focus {
      @include dsFocus();
    }
    @include icon-color(color(neutral, 200));
  }

  &.can-group-over {
    .#{$tree-item}__value {
      font-weight: bold;
    }
  }

  .#{$tree-item}-content {
    display: flex;
    flex: 1;
    min-width: 0;
  }

  .tree-item-icon {
    margin-right: space(xxs);
  }

  &:hover {
    .drag-handle {
      @include icon-color(color(brand-primary, 700));
    }

    @include dsFocusAfter();

    &:after {
      border-radius: 0px;
      box-shadow: unset;
    }
  }

  &:focus {
    @include dsFocusAfter();
    background-color: color(brand-primary, 200);

    &:after {
      box-shadow: unset;
      border: unset;
    }
    &:hover {
      @include dsFocusAfter();

      &:after {
        box-shadow: unset;
      }
    }
  }


  .toolbar-trigger{
    margin-top: space(xxs);
  }
}

.#{$tree-list}{
  &.#{$rz-compact}{
    .#{$tree-item} {
      min-height: $compact;
      &__addons {
        height: $compact;
      }
      &__value  {
        margin: 0;
        margin-top:space(xs);
      }
    }
    .toolbar-trigger{
      margin-top: 0;
    }
  }
}

.#{$tree-list}-no-items {
  display: flex;
  justify-content: center;
  padding-top: space(m);
  height: 100%;
  width: 100%;
}

.tree-text-highlight {
  font-weight: bold;
  background-color: color(warning, 500);
}
