
@mixin icon($size: s, $color: null) {
  $fetched-size: s;
  @if map-has-key($icon-data, $size) {
    $fetched-size: map-get($icon-data, $size);
  }
  height: $fetched-size;
  width:  $fetched-size;
  svg {
    height: $fetched-size;
    width:  $fetched-size;
  }
  @if $color != null {
    @include icon-color($color)
  }  
}

@mixin icon-color($color: color(neutral, 800)) {
  fill: $color;
}
