.#{$el-icon} {
  border: $border-type color(neutral, 200);
  @include border-radius(0 $radius $radius 0);
  padding: 0 calc(#{space(xxs)} * 0.5);

  &:hover:not([disabled]) {    
    border: $border-type color(brand-primary, 600);
  }

  &.disabled {
    border: $border-type color(neutral, 100);
    pointer-events: none;
  }
}
