// ********************************************
// container definition
$el-container: #{$prefix}-form-element-radio;
$el-container-error: #{$el-container}--error;
$el-container-disabled: #{$el-container}--disabled;
$el-container-read-only: #{$el-container}--readonly;


// ********************************************
// elements
$el-input: #{$el-container}__input;
$el-label: #{$el-container}__label;
$el-button: #{$el-container}__input-button;
$el-text: #{$el-container}__label-text;
$el-children: #{$el-container}__radio-children;

// ********************************************
// local
$border-type: solid 1px;

.#{$el-input} {
  position: absolute;
  opacity: 0;
  right: 0;
}

.#{$el-text} {
  @include body-text-style();
  transition: duration(base) easing(base);
  color: color(neutral, 800);
  line-height: 100%;
  position: relative;
  bottom: -1px; // fix - extra pixel below the text
}

.#{$el-button} { 
  border-radius: 50%!important;
  transition: duration(base) easing(base);
  width: space(s);
  min-width: space(s);
  height: space(s);
  margin-right: space(xs);
  border: $border-type color(neutral, 400);
  background: color(neutral, 000); 
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-self: flex-start;
  position: relative;
  cursor: pointer;

  &:after {
    @include border-radius(50%);
    transition: duration(base) easing(base);
    width: space(xs);
    height: space(xs);
  }
  &:hover {
    border: $border-type color(brand-primary, 600);
  }
}

.#{$el-label} {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.#{$el-children}{
  padding-left: space(m);
}


// ********************************************
// Default container & elements           *****
.#{$el-container} {
  padding-bottom: space(s);
  circle.unchecked{
    fill: color(neutral, 0);
  }

  .#{$el-label} { 
    &:hover {
      .#{$el-button} {
        border: $border-type color(brand-primary, 600);
      }
      .#{$el-input} {
        &:checked + .#{$el-button} {
          background-color: color(brand-primary, 200);
        }
      }      
    }
    &:active {
      .#{$el-button} {
        background-color: color(brand-primary, 200);
        box-shadow: unset;
      }
      circle.unchecked{
        fill: color(brand-primary, 200);
      }
    }
    &:focus {
      .#{$el-button} {
        background-color: color(brand-primary, 200);  
      }
      circle.unchecked{
        fill: color(brand-primary, 200);
      }    
    }      
  } 

  .#{$el-input} {
    &:focus {
      background-color: color(brand-primary, 200);
      border-radius: 50%;
      box-shadow: unset;
    }

    &:checked + .#{$el-button} {
      border-color:color(brand-primary, 800);
      box-shadow: unset;
      circle.checked{
        fill: color(brand-primary, 600);
      }
    }  
  }

  .#{$el-button} {
    &:focus {
      box-shadow: unset;
    }
  }  
}

// ********************************************
// Error container & elements             *****
.#{$el-container-error} {

  .#{$el-button} {
    border: $border-type color(danger, 900); 
  }

  .#{$el-label} { 
    &:hover {
      .#{$el-button} {
        border: $border-type color(danger, 900);
        box-shadow: unset;
      }  
    }
    &:active {
      .#{$el-button} {
        background-color: color(danger, 200);
        box-shadow: unset;
      }
      circle.unchecked{
        fill: color(danger, 200);
      }
    }   
  } 

  .#{$el-input} {
    &:checked + .#{$el-button} {
      border-color:color(danger, 900);
      box-shadow: unset;
      circle.checked{
        fill: color(danger, 900);
      }
    }
  }
}

// ********************************************
// Disabled container & elements          *****
.#{$el-container-disabled} {

  .#{$el-label} {
    cursor: not-allowed;
    &:hover {
      .#{$el-button} {
        border-color: color(neutral, 400);
      }
      .#{$el-input} {
        &:checked + .#{$el-button} {
          background-color: color(neutral, 080);
        }
      }       
    }
    &:active {
      .#{$el-button} {
        background: color(neutral, 080);
      }
      circle.unchecked{
        fill: color(neutral, 080);
      }      
    }
  }

  .#{$el-text} {
    color: color(neutral, 500);
  }

  .#{$el-button} {
    pointer-events: none;
    border-color: color(neutral, 400);
    background: color(neutral, 080);
    circle.unchecked{
      fill: transparent;
    }
    &:hover {
      border-color: color(neutral, 400);
    }    
  }

  .#{$el-input} {

    &:checked + .#{$el-button} {
      border-color:color(neutral, 400);
      circle.checked{
        fill: color(neutral, 500);
      }
    }

    &:checked + .#{$el-text} {
      color: color(neutral, 400);
    }
  }

}

// ********************************************
// Read only container & elements         *****
.#{$el-container-read-only} {

  .#{$el-label} {
    &:hover {
      .#{$el-button} {
        border-color: color(neutral, 400);
      }
    }
  }

  .#{$el-button} {
    border-color:color(neutral, 400);
  }

  .#{$el-input} {

    &:checked + .#{$el-button} {
      border-color:color(neutral, 400);
      circle.checked{
        fill: color(neutral, 400);
      }
    }
  }

}


