$shadows-data: (
        500: (0 27px 24px 0 rgba(0, 0, 0, 0.24), 0 40px 77px 0 rgba(0, 0, 0, 0.19)),
        400: (0 16px 28px 0 rgba(0, 0, 0, 0.24), 0 25px 55px 0 rgba(0, 0, 0, 0.19)),
        300: (0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19)),
        200: (0 6px 20px 0 rgba(0, 0, 0, 0.24), 0 8px 17px 0 rgba(0, 0, 0, 0.19)),
        100: (0 1px 5px 0 rgba(0, 0, 0, 0.13), 0 2px 4px 0 rgba(0, 0, 0, 0.2)),
        000: 0 0 0 0 transparent
) !default;

$shadows-data-alt: (
        500: (0 0 0 1px rgba(16,22,26,.1), 0 27px 24px rgba(16,22,26,.2), 0 40px 77px rgba(16,22,26,.2)),
        400: (0 0 0 1px rgba(16,22,26,.1), 0 16px 28px rgba(16,22,26,.2), 0 25px 55 rgba(16,22,26,.2)),
        300: (0 0 0 1px rgba(16,22,26,.1), 0 12px 15px rgba(16,22,26,.2), 0 17px 50 rgba(16,22,26,.2)),
        200: (0 0 0 1px rgba(16,22,26,.1), 0 2px 4px rgba(16,22,26,.2), 0 8px 24px rgba(16,22,26,.2)),
        100: (0 0 0 1px rgba(16,22,26,.1), 0 0px 0px rgba(16,22,26,.2), 0 0px 0px rgba(16,22,26,.2)),
        000: 0 0 0 0 transparent
) !default;

$drop-shadow-data:(
        500: (0 27px 24px rgba(0, 0, 0, 0.3)),
        400: (0 16px 28px rgba(0, 0, 0, 0.3)),
        300: (0 12px 15px rgba(0, 0, 0, 0.3)),
        200: (0 6px 20px rgba(0, 0, 0, 0.3)),
        100: (0 0px 2px rgba(0, 0, 0, 0.3)),
        000: 0 0 0 0 transparent
) !default;
