$value-container: #{$combo}__value-container;
.#{$value-container} {
  color: color(neutral, 800);
  height: $height;
  //   line-height: $height;
  padding: 0 space(xs) !important;
  span {
    height: calc(#{$height} - 2px);
    line-height: calc(#{$height} - 1px);
  }   
}

// multiselect
.combobox-v2-value-container {
  width: calc(100% - #{space(m) * 1.2});
  margin-right: auto;
  background: color(neutral, 000);
  @include border-radius($radius);
  &.has-value {
    width: calc(100% - #{space(m) * 2.2});
  }
  &.dropdown-indicator-removed {
    width: calc(100% - #{space(m) * 0});
  }
}
