$form-group: #{$prefix}-form-group;
$form-label: #{$prefix}-form-label;
$form-field: #{$prefix}-form-field;
$form-field-group: #{$prefix}-form-field-group;

// ********************************************
// dimsum components
$dsinput: #{$prefix}-input;

.#{$form-group} {
  display: flex;
  align-items: center;
  margin-bottom: space(s);

   .#{$dsinput} {
     width: 100%;
   }

   .#{$form-label} {
     @include labelStyle();
     text-align: right;
     padding-right: space(m);
     min-width: space(xxl) * 2;
   }
   .#{$form-field-group} {
     flex: 1;
     display: flex;
     justify-content: space-between;

     > div {
       flex: 1;
     }

     .#{$form-field} {
       flex: 1;

       &:first-child {
         margin-right: space(xs);
       }
     }
   }
}


