// ********************************************
// container definition
$el-container: #{$prefix}-query-builder-drag-group;
$el-icon: #{$prefix}-icon;


.#{$el-container}{
  cursor: move;
  padding: space(xxs) 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  .#{$el-icon}{
       &.additional-drag{
         position: relative;
         bottom: space(xxs);
       }
  }
  .separator{
    margin: space(xxs) 0;
    background: color(neutral, 100);
    height: 1px;
    width: 80%;
  }
}