$wrapper:#{$prefix}-week-days-week-days-picker;
$radio-group: #{$prefix}-radio-group;
$checkbox-group: #{$prefix}-checkbox-group;
// elements
$el-container: #{$prefix}-form-element-radio;
$el-input: #{$el-container}__input;
$el-label: #{$el-container}__label;
$el-button: #{$el-container}__input-button;
$el-text: #{$el-container}__label-text;

$cb-container: #{$prefix}-form-element-checkbox;
$cb-input: #{$cb-container}__input;
$cb-label: #{$cb-container}__label;
$cb-button: #{$cb-container}__input-button;
$cb-text: #{$cb-container}__label-text;

$week-radio-size: space(l) * 1.2;

.#{$wrapper}{
  padding-top: space(xs);
  padding-bottom: space(xxs);

  .#{$el-text},
  .#{$cb-text}{
    position: absolute;
    top: 0;
    left: 0;
    width: $week-radio-size;
    height: $week-radio-size;
    line-height: calc(#{$week-radio-size} * 1.1);
    text-align: center;
  }

  .#{$radio-group},
  .#{$checkbox-group}{
    flex: 1;
    height: $week-radio-size;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .#{$el-container},
    .#{$cb-container}{
      margin-right: space(xxs);
      
      .#{$cb-text}{
        margin:0;
      }
      .#{$cb-button}{
        &[aria-checked="true"]{
          color: color(neutral, 000);
          background-color: color(brand-primary, 600);
          border: $border-type color(brand-primary, 700);
          & + .#{$el-text},
          & + .#{$cb-text}{
            color: color(neutral, 000);
          }
        }
        &:focus,
        &.#{$btn-el-vfocus} {
          @include dsFocus();
          @include border-radius(50%);
        }
      }

      .#{$el-input},
      .#{$cb-input}{
        &:checked + .#{$el-button},
        &:checked + .#{$cb-button} {
          color: color(neutral, 000);
          background-color: color(brand-primary, 600);
          border: $border-type color(brand-primary, 700);
          & + .#{$el-text},
          & + .#{$cb-text}{
            color: color(neutral, 000);
          }
        }
        &:focus,
        &.#{$btn-el-vfocus} {
          @include dsFocus();
        }
      }
    }

  }

  .#{$el-label},
  .#{$cb-label}{
    position: relative;
    width: $week-radio-size;
    height: $week-radio-size;
    display: block;
  }

  .#{$el-button},
  .#{$cb-button} {
    @include border-radius(50%);
    transition: duration(base) easing(base);
    width: $week-radio-size;
    height: $week-radio-size;
    margin-right: space(xs);
    background: color(neutral, 100);
    border: none;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    align-self: flex-start;
    cursor: pointer;

    &:after {
      display: none;
    }

    &:hover {
      border: $border-type color(brand-primary, 600);
    }

    &:focus{
      @include dsFocus();
    }
  }
}


.#{$prefix}-week-days-week-days-picker__label{
  @include labelStyle();
}
.#{$prefix}-form-element-radio__input,
.#{$el-button}{

  &:focus{
    @include dsFocus();
  }
}
