$main-list: nestable;
$group: nestable-group;
$item: nestable-item;
$el-option: rule-container-options;
$el-list: nestable-list;
$query-builder-row: em-ds-query-builder-row;

.nested-main-group{
  position: relative;
  display: flex;
  flex-direction: row;

  .#{$main-list}{
    flex: 1;
  }
  .#{$el-list}{
    order: 0;
  }
  .#{$el-option}{
    order: 1;
  }

}


// main group
.nestable-item-type-group{
  position: relative;  
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  .nestable-list{
    order:0;
    flex: 1;
  }
  .nestable-item-name--group{
    order: 1;
  }

}


.#{$item}{
  &.is-dragging{
    .#{$query-builder-row}{
      @include box-shadow($row-shadow-drag);
    }
  }
}


.#{$group}{

  .#{$item}{
    margin-bottom: space(xs);
    &:last-child{
      margin-bottom: 0;   
    }
  }
}



.nestable-item {
  & > .nestable-list{
    
    &.nestable-child-count-single{
        .is-or-filter{
          .nestable-item-name--filter{
            background: $is-or-color;
          }
        }
        .is-and-filter{
          .nestable-item-name--filter{
            background:  $is-and-color;
          }
        }
        .nestable-item-name--filter{
          padding: 1px;
          padding-bottom: 20px;
          @include box-shadow($row-shadow-default-filter);    
        }
    }
  }
}

.nested-main-group {
  & > .nestable{
    
    & > .nestable-child-count-single{
