// ********************************************
// container definition
$el-container: #{$prefix}-loading-indicator;

.#{$el-container} {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 201; // loading indicator trumps all modals / overlays; https://jira.elliemae.io/browse/PUI-1712
}
