$el-label-from: #{$prefix}-datepicker__label-from;
$el-label-to: #{$prefix}-datepicker__label-to;


.#{$el-label-from},
.#{$el-label-to}{
  position: absolute;
  margin-top: 0;
  z-index: 1;
  color: color(neutral, 200);
  background: color(neutral, 000);
  padding: 0px calc(#{space(xxs)} * 1.5);  
  max-height: #{space(xxs)};
  
  span {
    position: relative;
    display: block;
    top: calc(#{space(s)} * (-0.625));
    max-width: calc(#{space(xl)} * 2);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow-y: hidden;
  }
}


.#{$el-label-from} {  
  margin-left: calc(#{space(xs)} * 1.125);
}

.#{$el-label-to} {  
  margin-left: calc(#{space(xxl)} * 2.5);
}