// ********************************************
// container definition
$el-container: #{$prefix}-datepicker-range;
$el-icon: #{$prefix}-range-picker__icon;
$el-nav-icon-left: #{$prefix}-range-picker__chevron-left;
$el-nav-icon-right: #{$prefix}-range-picker__chevron-right;

$el-day-contents: #{$prefix}-datepicker__day-contents;
$el-non-selected-date: #{$prefix}-range-picker__non-selected;

$el-container-inputs: #{$prefix}-datepicker-wrapper-input-date-range-picker-controller;
$el-container-button: #{$prefix}-datepicker-wrapper-button-date-range-picker-controller;
$el-overlay-range: #{$prefix}-datepicker-wrapper-day-picker-single-date-controller;

// ********************************************
// local vars
$border-type: solid 1px;
$border-range: calc(#{space(xxs)} * 0.75) solid color(neutral, 000);
$primary200: color(brand-primary, 200);

.#{$el-container-inputs} {
  display: flex;
  flex-direction: row;
  div.#{$prefix}-form-item {
    outline: none;
    box-shadow: none;
    margin-right: space(xs);
  }
}

.#{$el-container-button} {
  margin-bottom: space(xs);
}

.#{$el-container},
.#{$date-range-selector-container},
.#{$el-overlay-range} {
  .DateRangePicker {
    min-width: calc(#{space(xxl)} * (4.57));
  }

  .DateRangePickerInput {
    border: none;
    border-right: none;
  }

  .DateInput {
    margin-right: #{space(s)};
  }

  .DateInput_input {
    border: $border-type color(neutral, 200);
    @include border-radius($radius);

    &:disabled {
      background-color: color(neutral, 000);
    }
  }

  .DateRangePickerInput_arrow {
    display: none;
  }

  .DateInput_input__focused {
    border: $border-type color(brand-primary, 600);
    box-shadow: color(brand-primary, 400);
  }

  .DateRangePickerInput__disabled {
    background: color(neutral, 000);
    pointer-events: none;
    border-color: color(neutral, 100);
    color: color(neutral, 500);
    cursor: not-allowed;

    &:focus,
    &:hover {
      border-color: color(neutral, 100);
    }

    &:active {
      border-color: color(neutral, 100);
    }

    .DateInput_input__focused {
      border: $border-type color(neutral, 100) !important;
      box-shadow: none;
    }
  }

  .CalendarDay__default {
    padding: 0 !important;
    position: relative;
    border: none;
    background-color: color(neutral, 000);
    text-align: center;
    transition: all duration(fast) easing(excite);

    &:focus {
      outline: none;
    }
  }

  .CalendarDay__default:hover,
  .CalendarDay__default:focus {
    color: color(neutral, 000);

    .#{$el-day-contents} {
      border: $border-type color(brand-primary, 600);
      box-shadow: inset 0 0 0 1px color(brand-primary, 300);
      color: color(brand-primary, 600);
    }
  }

  .CalendarDay__selected {
    color: color(neutral, 000);
    background: color(brand-primary, 200);
  }

  .CalendarDay__selected:hover,
  .CalendarDay__selected:focus {
    color: color(neutral, 000);

    .#{$el-day-contents} {
      color: color(neutral, 000);
      background: color(brand-primary, 600);
      box-shadow: inset 0 0 0 1px color(brand-primary, 300);
    }
  }

  .CalendarDay__selected_span {
    &:after {
      content: "";
      border-top: $border-range;
      border-bottom: $border-range;
      @include target-ie {
        border-top: none;
        border-bottom: none;
      }
      position: absolute;
      width: 100%;
      z-index: 1;
      top: 0;
      bottom: 0;
      left: 0;
    }
    background: $primary200;
  }

  .CalendarDay__selected_start,
  .CalendarDay__selected_end {
    &:before {
      content: "";
      position: absolute;
      background-color: color(neutral, 000);
      top: 0;
      bottom: 0;
      z-index: 0;
    }

    &:after {
      content: "";
      border-top: $border-range;
      border-bottom: $border-range;
      @include target-ie {
        border-top: none;
        border-bottom: none;
      }
      position: absolute;
      width: 100%;
      z-index: 1;
      top: 0;
      bottom: 0;
      left: 0;
    }
  }

  .#{$el-non-selected-date} {
    .CalendarDay__selected_start {
      background-color: color(neutral, 000);
    }
  }

  .CalendarDay__selected_start {
    &.CalendarDay__selected_end {
      background-color: color(neutral, 000);
    }
  }

  .CalendarDay__selected_start {
    &:before {
      left: 0;
      right: 50%;
    }
  }

  .CalendarDay__selected_end {
    &:before {
      right: 0;
      left: 50%;
    }
  }

  .CalendarDay__hovered_span:hover,
  .CalendarDay__hovered_span {
    background: $primary200;
    &:after {
      content: "";
      border-top: $border-range;
      border-bottom: $border-range;
      position: absolute;
      width: 100%;
      z-index: 1;
      top: 0;
      bottom: 0;
      left: 0;
    }
  }

  .CalendarDay__hovered_span:hover {
    &:before {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      left: 50%;
      background: color(neutral, 000);
      z-index: 0;
    }
  }

  .DateRangePicker + .em-ds-datepicker__picker {
    @include border-radius($radius);
  }

  .CalendarMonth {
    background: color(neutral, 050);
    text-align: center;
    vertical-align: top;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .CalendarMonth_table {
    border-collapse: collapse;
    border-spacing: 0;
    background-color: color(neutral, 000);
    width: calc(#{space(l)} * 9.93) !important;
    margin-left: calc(#{space(xxs)} * (-3));
  }

  .CalendarMonthGrid__horizontal {
    left: 0 !important;
  }

  .DayPicker_weekHeaders {
    margin-left: 0 !important;
  }

  .#{$el-icon} {
    border: $border-type color(neutral, 200);
    border-radius: #{space(xxs)};
    display: inline-block;
    padding: calc(#{space(s)} * (0.625));

    &:hover {
      border: $border-type color(brand-primary, 600);
    }

    &.disabled {
      border: $border-type color(neutral, 100);
    }
  }
}
