// ********************************************
// container definition
$zoom: #{$prefix}-zoom-resize;
$separator: #{$prefix}-selectbox__indicator-separator;
$input-container: #{$prefix}-selectbox__input;
$selec: #{$prefix}-selectbox__control;
.#{$zoom}{
  max-width: calc(#{space(xl)} + #{space(xl)});
  .#{$separator}{
    display: none;
  }
  .#{$input-container}{
    input:focus-within{
      min-width: space(xl);
    }
  }
  .#{$selec}{
    &--is-focused{
      .#{$prefix}-selectbox__value-container--has-value{
        span{
          display: none;
        }
      }
    }
  }
}
