// ********************************************
// block
$el-icon: #{$prefix}-icon;

.#{$el-icon} {
  svg { @include icon() }
  @include icon();
  display: inline-flex;
  vertical-align: middle;
}

// ********************************************
// modifiers
$el-icon-xxs: #{$el-icon}--xxs;
$el-icon-xs: #{$el-icon}--xs;
$el-icon-s: #{$el-icon}--s;
$el-icon-m: #{$el-icon}--m;
$el-icon-l: #{$el-icon}--l;
$el-icon-xl: #{$el-icon}--xl;
$el-icon-xxl: #{$el-icon}--xxl;

.#{$el-icon-xxs} {
  @include icon(xxs);
}
.#{$el-icon-xs} {
  @include icon(xs);
}
.#{$el-icon-s} {
  @include icon(s);
}
.#{$el-icon-m} {
  @include icon(m);
}
.#{$el-icon-l} {
  @include icon(l);
}
.#{$el-icon-xl} {
  @include icon(xl);
}
.#{$el-icon-xxl} {
  @include icon(xxl);
}

@each $color, $saturationMap in $color-palette-data {
  @each $saturation, $value in $saturationMap {
    .#{$el-icon}--color-#{$color}-#{$saturation} {
        fill: $value !important;
    }
  }
}
