$menu-option: #{$combo}__option;
.#{$menu-option} {
  height: space(l) * 1.093;
  line-height: space(l) * 1.093;
  cursor: pointer;
  padding: 0 space(xs);
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden; // avoid overflow inside option
  &--is-focused, &:hover {
    @include dsHover();
  }
  &:active {
    background-color: color(brand-primary, 200);
  }  
  &--is-selected {
    background-color: color(brand-primary, 200);
    color: color(neutral, 800);
    svg {
      fill: color(neutral, 000);
    }
  }
}


$menu-list-multi: combobox-menu-list-is-multi;

.#{$menu-list-multi}{
  .#{$menu-option} {
    &--is-selected {
      background-color: color(brand-primary, 200);
    }
  }
  
}
