$wrapper: #{$prefix}-end-date-time-picker;
$inputs: #{$prefix}-end-date-time-picker-inputs;


$label: #{$wrapper}__label;
$input-time-outline: #{$prefix}-labeled-outline;

.#{$wrapper}{
  padding: space(s) 0;
  margin: space(xs) 0;
  // border-bottom: $border-row-separator;

  .#{$input-group}{
    flex: 1;
    & > div{
      width: 50%;

      .#{$input-time-outline}{
        margin-top: 0;
      }
    }
  }
}

.#{$inputs}{
  display: flex;
  flex-direction: row;
  margin-bottom: 300px;
}

.#{$label}{
  @include labelStyle();
}