$wrapper: #{$prefix}-date-time-predefined-range-selector;


// component overrides
$wrapper-datepicker-sp: SingleDatePickerInput;
$wrapper-input-group: #{$prefix}-input-addon-group;
$wrapper-datepicker: #{$prefix}-datepicker;
$wrapper-datepicker-picker: #{$prefix}-datepicker__picker;
$wrapper-datepicker-input: DateInput_input;
$wrapper-select: #{$prefix}-selectbox__control;

.#{$wrapper}{
  hr {
    margin: space(s) 0;
    border: solid border-size() * 0.5 color(neutral, 100);
  }
  .#{$wrapper-datepicker-sp},
  .#{$wrapper-input-group},
  .#{$wrapper-datepicker},
  .#{$wrapper-datepicker-picker},
  .#{$wrapper-datepicker-input},
  .#{$wrapper-select}{
    height: $input-height;
  }


  .#{$wrapper-datepicker-sp}{
    flex: 1;
  }
  .#{$wrapper-datepicker-input}{
    width: 100%;
  }

}
