$dg-group-header: #{$data-grid}-group-row-header;

.#{$dg-group-header}{
    min-height: 24px;
    background-color: color(brand-primary, 200);
    display: flex;
    align-items: center;
    padding: 0 $data-grid-cell-horizontal-padding;
    border-top: solid 1px color(brand-primary, 500);
    &__title{
        font-weight: font-weight(semibold);
        font-size: 12px;
    }
}