$cmp-prefix: #{$prefix}-radio-group;
$el-container: #{$prefix}-form-element-radio;


.#{$cmp-prefix}-form-item-layout-wrapper{
  display: inline-flex;
  .#{$cmp-prefix} {
    padding-top: space(xs);
  }
}

.#{$cmp-prefix} {
  display: flex;
  flex-wrap: wrap;

  &--vertical {
    flex-direction: column;
  }

  &--horizontal {
    .#{$el-container} {
      margin-right: space(l);
    }
  }
}