// ********************************************
// container definition
$date-time-picker: #{$prefix}-date-time-picker;
$time-picker: #{$prefix}-time-picker-menu;
$date-picker: #{$prefix}-datepicker-range-wrapper-day-picker-single-date-controller;

// ********************************************
// elements
$date-time-picker-wrapper: #{$date-time-picker}__wrapper;
$time-picker-wrapper: #{$date-time-picker}__time-picker-wrapper;
$time-picker-header: #{$date-time-picker}__time-picker-header;
$date-input: #{$date-time-picker}__date-input;

.#{$date-time-picker-wrapper} {
  display: flex;
  flex-direction: row;
  margin-top: space(xs);
  box-shadow: depth(200);
  background-color: color(neutral, 000); 

  .#{$time-picker-wrapper} {
    background-color: color(neutral, 050);  
    display: flex;
    flex-direction: column;      

    .#{$time-picker} {
      border: unset;    
      box-shadow: unset;
    }
  }

  .#{$date-picker} {
    border-right: border-size(xs) solid color(neutral, 200);

    .DayPicker {
      box-shadow: unset;
    }
  }
}

.#{$date-input} {
  padding-right: space(xs);
}