.#{$group-box-content} {
  padding: space(m) 0;
  border: $theme-base-border-style color(neutral, 200);

  &--no-border-top {
    border-top: none;
  }

  &.#{$group-box-content}--group-box {
    padding: space(m) 0;
  }
  &--only-header {
    border: none;
  }
  &--no-border {
      border: none;
      .#{$group-box-label} {
        border-bottom: none;
      }
  }
}

// as row
.#{$group-box-content} {
  &--as-row {
    display: flex;
    flex-direction: row;
    padding-left: 0;
    padding-right: 0;

    .#{$group-box-wrapper}.#{$group-box-wrapper}--main-box {
      .#{$group-box-content} {
        padding-left: 0;
      }
  }

    .#{$group-box-wrapper} {
      flex: 1;
      margin-left: space(m);
    }
    .#{$group-box-wrapper}:first-child {
      margin-left: 0;
      border-left: none;
    }
    .#{$group-box-wrapper}:last-child {
      border-right: none;
    }
  }
  &--no-padding {
    padding-top: 0;
    padding-bottom: 0;
  }
  &--no-border {
    border: none;
    .#{$group-box-wrapper} {
      border: none;
    }
  }
}
