$wrapper-btn: #{$prefix}-query-builder-addon-search-field;
$btn: #{$wrapper-btn}__btn-selector;
$btn-empty: #{$btn}--btn-placeholder;
$dropdown-selector:#{$wrapper-btn}__dropdown-field-selector;
$dropdown-container: #{$wrapper-btn}-container-selector;
$advance-search-option-btn:advance-search-option-btn;


.#{$dropdown-container}{
  display: flex;
  width: 100%;
}

.#{$btn}{
  height: $basic-row-height;
  padding:0 space(xxs);
  overflow: hidden;
  text-align: left;
  justify-content: left;
  color: color(neutral, 500);
  border: none;
  background: none;
}
.#{$btn-empty}{
  color: color(neutral, 100);
  font-style: italic;
}
.#{$dropdown-selector}{
  width: 100%;
}

.#{$advance-search-option-btn}{
  height: $basic-row-height;
  width: 100%;
}

.advanced-search-option {
  border-top: solid 1px color(neutral, 100);
}

.modal-search-field {
  .datagrid-tab-content {
    height: calc(100vh - #{space(xxl) * 4});
  }
}
