$btn: #{$prefix}-button;
$btn-dropdown: dropdown-indicator;



.#{$btn}.#{$btn-dropdown} {
  &.active {
    border: none;
    box-shadow: none;
    &:after {
      box-shadow: none !important;
      border-color: transparent;
    }
    @include dsBorderAfter(color(brand-primary, 700), 1);
    border-color: solid color(brand-primary, 700) 1px;
  }
}
