.#{$prefix}-modal.insert-field-modal {
  max-width: 300px;
  :first-child {
    max-width: 100%;
  }
}
.insert-field-modal {
  .#{$el-body} {
    padding: 0px;
    overflow-y: hidden;
  }
  .#{$el-body-wrapper} {
    padding: 0px;
  }
  .#{$el-overlay} {
    z-index: 10;
  }
  .wrapper {
    display: flex;
    .col-1 {
      flex: 3;
      margin: space(s) 0;
      overflow-y: auto;
      max-height: 60vh;
      .title {
        font-size: font-size(600);
        font-weight: font-weight(semibold);
        position: sticky;
        padding: 0 space(s) space(s);
        top: 0;
        background-color: white;
        text-align: center;
        span:first-of-type {
          padding-right: space(s);
        }
      }
      .insertfieldmodal-element {
        padding: 1em 0;
        outline-style: none;
        .content {
          padding: 0 space(s);
          .label {
            color: color(neutral, 500);
            padding-left: space(s);
            padding-right: space(xs);
            font-weight: font-weight(regular);
          }
          .value {
            font-weight: font-weight(semibold);
          }
        }
        &:hover {
          background-color: color(neutral, 100);
          cursor: pointer;
        }
        &:focus {
          background-color: color(brand-primary, 200);
        }
      }
    }
  }
}