$icon-selector :#{$prefix}-icon;

.#{$prefix}-modal-feedback{
  display: flex;
  justify-content: center;
  padding-bottom: space(s);

  .#{$icon-selector}{
    height: space(xxl);
    width: space(xxl);
  }

  &--modal-type-warning{
    .#{$icon-selector}{
      @include icon-color(color(warning, 700));
    }
  }

  &--modal-type-error{
    .#{$icon-selector}{
      @include icon-color(color(danger, 900));
    }
  }

  &--modal-type-success{
    .#{$icon-selector}{
      @include icon-color(color(success, 900));
    }
  }

  &--modal-type-help{
    .#{$icon-selector}{
      @include icon-color(color(brand-primary, 400));
    }
  }

  &--modal-type-info{
    .#{$icon-selector}{
      @include icon-color(color(brand-primary, 400));
    }
  }
}