$card-nav: #{$prefix}-card-navigation;
$wrapper: #{$card-nav}__wrapper;
$central-content: #{$card-nav}__central-content;
$icon: #{$card-nav}__icon;
$content: #{$card-nav}__content;
$title-wrapper: #{$card-nav}__title-wrapper;
$title: #{$card-nav}__title;
$description: #{$card-nav}__description;
$actions: #{$card-nav}__actions;
$action: #{$card-nav}__action;

$wrapper-width:calc(#{space(xl)} * 7);
$icon-width: calc(#{space(xl)} * 1.3);
$content-width: calc(#{$wrapper-width} - #{$icon-width});
$b-radius: space(xxs) * 0.5;


.#{$wrapper} {
  width: $wrapper-width;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  @include box-shadow(depth(100));
  border-radius: $b-radius;
  position: relative;
  outline: none;
  &--fluid {
    width: 100%;
  }
  :after{
    display:block;
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    border: solid space(xxs) * 0.25 transparent;
    border-radius: $b-radius;
    pointer-events: none;
  }
  &:hover, &:focus {
    @include box-shadow(depth(200));
    :after{
      border-color: color(brand-primary, 600);
    }
    
  }
}

.#{$icon} {
  width: $icon-width;
  height: calc(#{space(xl)} * 1.25);
  background-color: #32aad1;
  border-top-left-radius: $b-radius;
  border-bottom-left-radius: $b-radius;
  padding: space(xs);
  display: flex;
  align-items: center;
  justify-content: center;
  fill: color(neutral, 0);
  &--borrower {
    background-color: #32aad1;
  }
  &--financial {
    background-color: #1ba88d;
  }
  &--loan {
    background-color: #8e49b2;
  }
  &--regulatory {
    background-color: #db5575;
  }
}

.#{$central-content}{
  width: $content-width;
  display: flex;
}

.#{$content} {
  padding-right: space(s);
  padding-left: space(s);
  display: flex;
  align-items: center;
  flex: 1;
  overflow: hidden;
}

.#{$title-wrapper} {
  width: 100%;
}
.#{$title} {
  padding-top: space(xs);
  @include h3-text-style(semibold);
}

.#{$description} {
  @include body-text-style();
  color: color(neutral, 500);
}
.#{$actions}{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: space(xs);
  .#{$action}{
    margin-left: space(xs);
    .#{$prefix}-icon{
      height: space(m);
      width:  space(m);
      fill: color(neutral, 500);
      svg {
        height: space(m);
        width:  space(m);
      }
    }
  }
}