$group-box-label: #{$prefix}-group-box__label;

.#{$group-box-label} {
  &--category-box {
    width: 100%;
    height: space(l) * 1.1252;
    padding:0 0;
    color: color(neutral, 800);
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: $theme-base-border-style color(neutral, 200);
  }

  &--no-border{
    border: none;
  }

  &.#{$group-box-label}--group-box {
    border: unset;
    font-size: font-size(500);
    padding: 12px 0;

    &.#{$group-box-label}--border-bottom {
      border-bottom: $theme-base-border-style color(neutral, 200);
    }
  }
}
