$date-range-picker: #{$prefix}-date-range-picker;
$input-addon-group: #{$prefix}-input-addon-group;

.#{$date-range-picker}{
  &__header {
    display: flex;
    justify-content: center;
    width: fit-content;
    flex-grow: 1;
    width: 0;
    background: #fff;
    padding: space(xs);
    .#{$input-addon-group} {
      width: 100%;
    }
    &--invalidDate{
      .#{$input-addon-group} {
        .#{$prefix}-input {
          border: solid 1px color(danger, 900);
        }
      }
    }
  }
}