$el-container: #{$prefix}-datepicker;

.#{$el-container},
.#{$picker-classname},
.#{$day-picker-classname}{
  .DayPickerNavigation {
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 0;
    right: 0;
    top: calc(#{space(xs)} * 1.25);
    padding: space(xxs) space(xs) 0 space(xs);
  }

  .CalendarMonth_caption {
    padding-top: calc(#{space(xs)} * 1.25);
    padding-bottom:  calc(#{space(l)} * 1.187);
    font-size: font-size(500);
    line-height: 1;
    strong {
      position: relative;
      top: space(xs);      
    }
  }
}
