$date-switcher: #{$prefix}-date-switcher;
$date-header: #{$prefix}-date-picker-header;
.#{$date-switcher}{
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: #fff;
  }

  &__header > .#{$date-header} {
    display: flex;
    justify-content: flex-end;
  } 
}

.switch-header__checkbox {
  padding: 0px;
  margin-left: space(s) * 0.4;
}