@import './DatePickerHeader/DatePickerHeader';
@import './DatePickerRange/DatePickerRange';
@import './DatePickerSingleDate/DatePickerSingleDate';
@import './DatePickerRangeHeader/DatePickerRangeHeader';
@import './SingleRangeDateSwitcher/SingleRangeDateSwitcher';


// ********************************************
// container definition
$el-container: #{$prefix}-datepicker;
$el-inputs: #{$prefix}-datepicker-inputs;
$el-input: DateInput_input;
$el-day-contents: #{$prefix}-datepicker__day-contents;
$el-icon: #{$prefix}-datepicker__picker;

$el-overlay-container: em-ds-datepicker-range-wrapper-day-picker-single-date-controller;
$el-overlay-range: #{$prefix}-datepicker-wrapper-day-picker-single-date-controller;
// $el-overlay-range: #{$prefix}-datepicker-wrap-day-picker;

// ********************************************
// container definition
$el-focused: #{$el-container}--focused;

// ********************************************
// vars
$border-type: solid 1px;
$radius: calc(#{space(xxs)} * 0.5);
$input-height: calc(#{space(m)} * 1.16666);


// ********************************************
// Input wrapperRef
.#{$el-inputs} {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

// ********************************************
// Input Text Styling Vars
.#{$el-input} {
  @include border-radius($radius 0 0 $radius);
  transition: border duration(base) easing(base);
  border: $border-type color(neutral, 200);
  border-right: none;
  height: $input-height;
  padding: 0 space(xs);
  outline: none;
  box-sizing: border-box;

  &:focus,
  &:hover {
    border-color: color(brand-primary, 600);
  }

  &:active {
    border-color: color(brand-primary, 700);
    color: color(neutral, 800);
  }

  @include placeholder {
    color: color(neutral, 100);
    font-style: italic;
  }
}
.#{$el-input}__disabled {
  background: color(neutral, 000);
  pointer-events: none;
  border-color:color(neutral, 100);
  color: color(neutral, 500);
  cursor: not-allowed;

  &:focus,
  &:hover {
    border-color:color(neutral, 100);
  }

  &:active {
    border-color:color(neutral, 100);
  }
}
// ********************************************
// container
.#{$el-container},
.#{$picker-classname},
.#{$date-range-selector},
.#{$el-overlay-container},
.#{$el-overlay-range} {

  .DateInput {
    width: auto;
  }

  .CalendarMonth {
    background: color(neutral, 050);
  }
  
  .CalendarDay__default, 
  .CalendarDay__selected, 
  .CalendarDay__selected:active, 
  .CalendarDay__selected:hover {
    background: color(neutral, 000);
    text-align: center;
    border: none;
    &:focus {
      outline: none;
    }
  }

  .CalendarDay__default:hover,
  .CalendarDay__default:focus{
    background: color(neutral, 000);
    border: none;
    .#{$el-day-contents} {
      background: color(neutral, 000);
      border: $border-type color(brand-primary, 600);
      color: color(brand-primary, 600);
    }
  }

  .CalendarDay__selected {
    background: #fff;
    color: color(neutral, 000);
    border: none;
    .#{$el-day-contents},
    .#{$prefix}-datepicker-range__day-contents {
      background-color: color(brand-primary, 600);
      border: $border-type color(brand-primary, 600);
      color: color(neutral, 000);
      &:hover {
        box-shadow: inset 0 0 0 1px color(brand-primary, 300);
      }
    }

    &.CalendarDay__selected_start.CalendarDay__selected_end{
      .#{$el-day-contents},
      .#{$prefix}-datepicker-range__day-contents {
        background-color: color(brand-primary, 600);
        border: $border-type color(brand-primary, 600);
        color: color(neutral, 000);
        background: -moz-linear-gradient(-45deg, color(brand-primary, 600) 0%, color(brand-primary, 600) 50%, color(brand-primary, 800) 50%, color(brand-primary, 800) 100%);
        background: -webkit-linear-gradient(-45deg, color(brand-primary, 600) 0%,color(brand-primary, 600) 50%,color(brand-primary, 800) 50%,color(brand-primary, 800) 100%);
        background: linear-gradient(135deg, color(brand-primary, 600) 0%,color(brand-primary, 600) 50%,color(brand-primary, 800) 50%,color(brand-primary, 800) 100%); 

      }
    }
  }

  .CalendarDay__selected:hover {
    color: color(neutral, 000);
    .#{$el-day-contents},
    .#{$prefix}-datepicker-range__day-contents {
      background-color: color(brand-primary, 600);
      color: color(neutral, 000);
    }
  }

  .CalendarDay.CalendarDay__blocked_calendar{
    color: color(neutral, 500);
    & > div.#{$prefix}-datepicker-range__day-contents {
      border: none;
      color: color(neutral, 500);
      pointer-events: none;
      position: relative;
      &::after{
        content: '';
        height: space(xxs) * 0.25;
        width: 48%;
        background: color(neutral, 500);
        position: absolute;
        pointer-events: none;
        top: 50%;
        margin-top: space(xxs) * (-0.15);
        left: 26%;
      }
    }
    cursor: not-allowed;
  }

  .CalendarDay.CalendarDay__outside{
    color: color(neutral, 500);
    & > div.#{$prefix}-datepicker-range__day-contents {
      border: none;
      color: color(neutral, 200);
      pointer-events: none;
    }
    pointer-events: none;
  }

  .CalendarMonth_table {
    border-collapse: collapse;
    border-spacing: 0;
    background-color: color(neutral, 000);
    width: calc(#{space(l)} * 9.93) !important;
    margin-left: calc(#{space(m)} * (-0.5));
    margin-top: #{space(s)};
  }

  .DayPicker_weekHeader {
    position: absolute;
    color: color(neutral, 800);    
    height: space(m);
    top: calc(#{space(l)} * 1.55);
    z-index: 2; //required
    text-align: left;
  }

  .DayPicker_weekHeader_ul {
    font-size: font-size(500);
  }
  
  .DayPicker_weekHeader_li {
    display: inline-block;
    text-align: center;
    width: calc(#{space(xs)} * 5.625) !important;
  }

  .DayPicker_weekHeader_li {
    small {
      font-size: font-size(400);
    }
  }

  // .DayPicker_transitionContainer {
  //     // height: calc(#{space(m)} * 12.625) !important;
  // }

  .DayPicker__withBorder {
      box-shadow: depth(200);
  }

}

// ********************************************
// ON Focus
.#{$el-focused}{

  .#{$el-input}{
    border-color: color(brand-primary, 600);
    @include box-shadow(none); 
  }
  .#{$el-icon}:not([disabled]){
    border-right-color: color(brand-primary, 600);
    border-top-color: color(brand-primary, 600);
    border-bottom-color: color(brand-primary, 600);
    @include box-shadow(none); 
  }

}