// ********************************************
// container definition
$banner-wrapper: #{$prefix}-banner;
$banner-wrapper-default: #{$banner-wrapper}--default;
$banner-wrapper-info: #{$banner-wrapper}--info;
$banner-wrapper-warning: #{$banner-wrapper}--warning;
$banner-wrapper-danger: #{$banner-wrapper}--danger;
$banner-wrapper-success: #{$banner-wrapper}--success;

$banner-label: #{$banner-wrapper}__label;
$banner-close: #{$banner-wrapper}__close;
$banner-icon: #{$banner-wrapper}__icon;
$banner-body: #{$banner-wrapper}__body;

// ********************************************

.#{$banner-wrapper} {
  width: 100%;
  min-height: calc(#{space(l)} + #{space(xxs)});
  color: color(neutral, 000);
  display: flex;
  align-items: center;
  svg {
    fill: color(neutral, 000);
  }
}
.#{$banner-wrapper-default} {
  background: #52a6ec;
}
.#{$banner-wrapper-success} {
  background: color(success, 900);

  .#{$banner-icon} {
    span.#{$prefix}-icon {
      background: white;
      border-radius: 50%;
    }
    svg {
      fill: #62d39f;
    }
  }
}
.#{$banner-wrapper-info} {
  background: #52a6ec;
  .#{$banner-icon} {
    span.#{$prefix}-icon {
        position: relative;
        &:after{
            position: absolute;
            top: 5%;
            left: 5%;
            display: block;
            content: "";
            width: 90%;
            height: 90%;
            background: white;
            border-radius: 50%;
        }
    }
    svg {
        position: relative;
        z-index: 1;
      fill: #89C4F4;
    }
  }
}
.#{$banner-wrapper-warning} {
  background: color(warning, 700);

  .#{$banner-icon} {
    span.#{$prefix}-icon {
      background: white;
      border-radius: 50%;
    }
    svg {
      fill: #FFBB51;
    }
  }
}
.#{$banner-wrapper-danger} {
  background: color(danger, 900);
}

.#{$banner-icon} {
  padding: space(xs);
  padding-left: space(s);
}
.#{$banner-label} {
  @include section-header-text-style(semibold);
  text-transform: initial;
  font-size: font-size(500);
  padding-right: space(s);
  padding-top: calc(#{space(xxs)} * 0.5);
}
.#{$banner-body} {
  @include section-header-text-style(normal);
  text-transform: initial;
  font-size: font-size(500);
  padding-right: space(xs);
  padding-top: calc(#{space(xxs)} * 0.5);
}
.#{$banner-close} {
  margin-left: auto;
  padding: space(xs);
  padding-right: space(s);
}
