$wrapper: #{$prefix}-timezone-picker-input-range-selector;
$label: #{$wrapper}__label;
$input-group: #{$prefix}-input-addon-group;


.#{$wrapper}{
  padding: 0 0 space(s) 0;
  margin: 0 0 space(xs) 0;
  .#{$input-group}{
    flex: 1;
    & > div{
      width: 50%;
    }
  }
}

.#{$label}{
  @include labelStyle();
}
