// ********************************************
// Clean default style
.SingleDatePickerInput {
  border: none;
  border-right: none;
}
.CalendarDay__default {
  padding: 0 !important;
  position: relative;
  border: none;
  &:hover {
    border: none;
  }
}

.CalendarDay__outside {
  pointer-events: none;
}

.CalendarMonth {
  text-align: center;
  vertical-align: top;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.CalendarMonthGrid__horizontal {
  left: 0 !important;
}

.DayPicker_weekHeaders {
  margin-left: 0 !important;
}

.DayPicker_weekHeader {
  padding: 0 !important;  
}

.DayPicker_weekHeader_ul {
  list-style: none;
  margin: 0;
  padding-left: 0;
  padding-right: 0; 
}