.#{$prefix}-field-wrapper{
  display: flex;
  flex-direction: row;
  .#{$prefix}-field-wrapper{
    padding-right: space(xs);
    flex: 1;
  }
  .#{$prefix}-field-wrapper:last-child{
    padding-right: 0;
  }
}


.#{$prefix}-field-wrapper{
  .#{$prefix}-input,
  .#{$prefix}-selectbox-wrapper,
  .#{$prefix}-datepicker,
  .#{$prefix}-time-picker .#{$prefix}-input-addon-wrapper{
    width: 100%;
  }
  .#{$prefix}-selectbox__control{
    padding-right: 0;
  }
  .#{$prefix}-input-addon-group *:not(:last-child){
    margin-right: 0;
  }
  .#{$prefix}-input-addon-wrapper__addon .#{$prefix}-input-addon-wrapper__addon--default .#{$prefix}-input-addon-wrapper__addon--right{
    &:before{
      display: none;
    }
  }

}